export default {
  name: 'Grids',
  props: ['entrylist'],
  data () {
    return {}
  },
  methods: {
    handleRouteSkip (item) {
      console.log(item)
      switch (item.type) {
        case 'article':
          this.$router.push({ path: item.type, query: { id: item.articleId } })
          break
        case 'map':
          this.$router.push({ path: item.type, query: { address: item.location } })
          break
        case 'weather':
          this.$router.push({ path: item.type, query: { city: item.city, date: item.date } })
          break
        case 'file':
          localStorage.setItem('fileList',JSON.stringify(item.fileList));
          this.$router.push({ path: item.type})
          break
        case 'link':
          window.location.href = item.href;
          break
        default:
          this.$router.push(item.type)
          break
      }
    }
  }
}



