export default {
  name: 'Weather',
  data () {
    return {
      list: [], // 总数据
      today: {},
      forecast: [], // 5天数据
      showHideOnBlur: false, // 开关蒙版
      deltaT: 0, // 相差天数
      ceshi: 3 //测试天数
    }
  },
  methods: {
    requestWeatherInfo () {
      let city = this.$route.query.city
      fetch(`http://wthrcdn.etouch.cn/weather_mini?city=${city}`)
        .then((res) => {
          return res.json()
        })
        .then((res) => {
          this.list = res.data
          this.forecast = res.data.forecast
          console.log(this.list)
          this.today = this.forecast[0]
          this.handleXsDate() // 比较日期
        }).catch((err) => {
          console.log('err' + err)
        })
    },
    GetDateDiff (startTime, endTime, diffType) {
    // 将xxxx-xx-xx的时间格式，转换为 xxxx/xx/xx的格式
      startTime = startTime.replace(/\-/g, '/')
      endTime = endTime.replace(/\-/g, '/')
      // 将计算间隔类性字符转换为小写
      diffType = diffType.toLowerCase()
      var sTime = new Date(startTime) // 开始时间
      var eTime = new Date(endTime) // 结束时间
      // 作为除数的数字
      var divNum = 1
      switch (diffType) {
        case 'second':
          divNum = 1000
          break
        case 'minute':
          divNum = 1000 * 60
          break
        case 'hour':
          divNum = 1000 * 3600
          break
        case 'day':
          divNum = 1000 * 3600 * 24
          break
        default:
          break
      }
      return parseInt((+eTime.getTime() - sTime.getTime()) / parseInt(divNum))
    },
    handleXsDate () { // 获取时间差
      let endtime = (new Date(this.$route.query.date)).Format('yyyy-MM-dd hh:mm:ss')
      let startTime = (new Date()).Format('yyyy-MM-dd hh:mm:ss')
      var deltaT = this.GetDateDiff(startTime, endtime, 'day')
      console.log(startTime, endtime, deltaT)
      if (deltaT > 4) {
        this.showHideOnBlur = true
      } else {
        this.deltaT = deltaT
      }
    }
  },
  computed: {
    forecasts () {
      return this.forecast.filter((item) => {
        item.date = item.date.split('星期')[0]
        item.high = item.high.split('温 ')[1]
        item.low = item.low.split('温 ')[1]
        return item
      })
    }
  },
  created () {
    this.requestWeatherInfo()
  }
}



