import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Main/Home'
import Weather from '@/views/Weather'
import MyJourney from '@/views/MyJourney'
import Map from '@/views/Map'
import PersonList from '@/views/PersonList'
import Article from '@/views/Article'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/article',
      name: 'Article',
      component: Article
    },
    {
      path: '/weather',
      name: 'Weather',
      component: Weather
    },
    {
      path: '/trip',
      name: 'MyJourney',
      component: MyJourney
    },
    {
      path: '/map',
      name: 'Map',
      component: Map
    },
    {
      path: '/file',
      name: 'PersonList',
      component: PersonList
    }
  ]
})



