import HomeSwiper from './Swiper'
import Grids from './Grids'
export default {
  name: 'Home',
  components: {
    HomeSwiper,
    Grids
  },
  data () {
    return {
      data: {},
      swiperlist: [], // 轮播数据
      gridlist: [], // 九宫格数据
      headTitle: '最具价值的生态城市运营商'
    }
  },
  mounted () {
    fetch('http://mt.guoanfamily.com/handbook/HandBookbyId?id=6')
      .then(res => { return res.json() })
      .then(res => {
        this.data = JSON.parse(res.Data.Fpjson)
        this.swiperlist = this.data.carouselArray
        this.gridlist = this.data.entryArray
        console.log(this.gridlist)
      })
  }
}



