export default {
  name: 'Article',
  data () {
    return {
      Pagedetail: ''
    }
  },
  methods: {
    requestAritcleInfo () {
      var Id = this.$route.query.id
      fetch(`http://mt.guoanfamily.com/handbook/HandBookDetailbyId?id=${Id}`)
        .then(res => { return res.json() })
        .then((res) => {
          // console.log(res)
          if (res.Code === 200 && res.Msg === 'ok') {
            this.Pagedetail = res.Data.Pagedetail
          }
        })
    }
  },
  watch: {
    '$route': 'requestAritcleInfo'
  },
  mounted () {
    this.requestAritcleInfo()
  }
}



